.form {
    width: 100%;
    max-width: 358px;
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    background-color: black;
}

@media screen and (max-width: 379px) {
    .form {
        margin: 0 auto 173px;
        padding: 0 30px 0;
    }
}