.header__link {
    display: block;
    margin: 0 0 0 24px;
    align-self: center;
    padding: 0;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: rgba(169, 169, 169, 1);
    transition: 0.3s;
    border: 0;
    background-color: transparent;
  }
  
  .header__link:hover {
    opacity: 0.6;
    cursor: pointer;
  }
  
  @media (max-width: 320px) {
    .header__link {
      font-size: 14px;
      line-height: 17px;
      display: none;
    }
  }
  